/*SUN*/

.sun{
    position: relative;
    height: 150px;
    width: 150px;
    background-color: rgb(253, 255, 116);
    border-radius: 50%;
    display: inline-block;
    border: 1px;
    transition: 1s;
    -webkit-animation: sunrise;
            animation: sunrise;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes sunrise{
    0% {right: -1000px}
    100% {right: -80px}
}

@keyframes sunrise{
    0% {right: -1000px}
    100% {right: -80px}
}

.sun:hover{
    height: 160px;
    width: 160px;
}

/*moon*/
.moon{
    position: relative;
    height: 150px;
    width: 150px;
    background-color: rgb(240, 240, 240);
    border-radius: 50%;
    display: inline-block;
    border: 1px;
    transition: 1s;
    -webkit-animation: moonsisr;
            animation: moonsisr;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    box-shadow: 0px 0px 20px 10px rgba(255, 255, 255, 0.616);
}

@-webkit-keyframes moonsisr{
    0% {right: -1000px}
    100% {right: -80px}
}

@keyframes moonsisr{
    0% {right: -1000px}
    100% {right: -80px}
}

.moon:hover{
    height: 160px;
