/*CLOUD*/

.cloud{
    margin-top: -50px;
    margin-left: -30px;
    position: relative;
    width: 300px;
    height: 100px;
    border-radius: 50px;
    background: rgb(255, 255, 255);
    z-index: 9;

    transition: 1s;
    -webkit-animation: cloud;
            animation: cloud;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.cloud:hover{
    height: 110px;
    width: 310px;
}

.cloud::before{

    content: '';
    position: absolute;
    top:-50px;
    right:40px;
    background: #fff;
    width: 100px;
    height: 100px;
    border-radius: 50%;

}
.cloud::after{
    content: '';
    position: absolute;
    top:-75px;
    left: 45px;
    background: #fff;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    z-index: 9;
}

@-webkit-keyframes cloud{
    0% {right: -1000px}
    100% {right: 0px}
}

@keyframes cloud{
    0% {right: -1000px}
    100% {right: 0px}
}

/*DARK CLOUD*/

.Dark_cloud{
    margin-top: 0px;
    position: relative;
    width: 300px;
    height: 100px;
    border-radius: 50px;
    background: rgb(61, 61, 61);
    -webkit-animation: Dcloud 1.5s;
            animation: Dcloud 1.5s;
    transition: 1s;
    
    
}

.Dark_cloud:hover{
    height: 110px;
    width: 310px;
}

.Dark_cloud::before{

    content: '';
    position: absolute;
    top:-50px;
    right:40px;
    background: rgb(61, 61, 61);
    width: 100px;
    height: 100px;
    border-radius: 50%;

}
.Dark_cloud::after{
    content: '';
    position: absolute;
    top:-75px;
    left: 45px;
    background: rgb(61, 61, 61);
    width: 150px;
    height: 150px;
    border-radius: 50%;
}
@-webkit-keyframes Dcloud{
    0% {right: -1000px}
    100% {right: 0px}
}
@keyframes Dcloud{
    0% {right: -1000px}
    100% {right: 0px}
}

