/*TUNDER*/

.Tunder
{
    display: flex;
    -webkit-animation: tundersMove 1.5s,tunders infinite;
            animation: tundersMove 1.5s,tunders infinite;
    -webkit-animation-duration: 1s ;
            animation-duration: 1s ; 
    
       
}
@-webkit-keyframes tundersMove{
    0% {left:1000px;}
    100%{left:60px;}
}
@keyframes tundersMove{
    0% {left:1000px;}
    100%{left:60px;}
}
@-webkit-keyframes tunders{
    0% {
        opacity: 1;
    }
    10% {
        opacity: 1;
    }
    20% {
        opacity: 0;
    }
    50% {
        opacity: 0;
    }
    85% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
@keyframes tunders{
    0% {
        opacity: 1;
    }
    10% {
        opacity: 1;
    }
    20% {
        opacity: 0;
    }
    50% {
        opacity: 0;
    }
    85% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

.Tunder .shape1{
    position: relative;
    width: 45px;
    height: 90px;
    right:-60px;
    background: rgb(238, 188, 80);
    background: var(--clr);
    -webkit-transform: skew(-25deg) translate(-50%,-50%);
            transform: skew(-25deg) translate(-50%,-50%);
    box-shadow: 10px 40px 45px 10px rgb(255, 248, 234);
    -webkit-animation: tundersMove 1.5s;
            animation: tundersMove 1.5s;
    transition: 1s;
}

.Tunder .shape1::before{
    content: ' ';
    position: absolute;
    bottom: -80px;
    right:-30px;
    width: 0;
    height: 0;
    border-top: 100px solid rgb(238, 188, 80);
    border-top: 100px solid var(--clr);
    border-right :50px solid transparent;
}

