/*MIST*/

.mist{
    display: flex;
    flex-direction: row-reverse;
    content: ' ';
    position: absolute;
    left: 3px;
    top: -80px;
    width: 180px;
    height: 20px;
    border-radius: 30%;
    color: ver(--rain);
    box-sizing: border-box;
    -webkit-animation:mistcloud 1.5s, misting 5s linear infinite;
            animation:mistcloud 1.5s, misting 5s linear infinite;
}

@-webkit-keyframes mistcloud{
    0% {left:1000px;}
}

@keyframes mistcloud{
    0% {left:1000px;}
}

@-webkit-keyframes misting{
    0% {
        box-shadow:
            0px 50px 25px 5px rgb(250, 253, 255),
            30px 90px 25px 5px rgb(250, 253, 255),
            15px 130px 25px 5px rgb(250, 253, 255);
        box-shadow:
            0px 50px 25px 5px var(--snow),
            30px 90px 25px 5px var(--snow),
            15px 130px 25px 5px var(--snow);         
    }
    50%{
        box-shadow:
            -10px 50px 10px 10px rgb(250, 253, 255),
            40px 90px 10px 10px rgb(250, 253, 255),
            5px 130px 10px 10px rgb(250, 253, 255);
        box-shadow:
            -10px 50px 10px 10px var(--snow),
            40px 90px 10px 10px var(--snow),
            5px 130px 10px 10px var(--snow);    
    }
    100%{
        box-shadow:
            0px 50px 25px 5px rgb(250, 253, 255),
            30px 90px 25px 5px rgb(250, 253, 255),
            15px 130px 25px 5px rgb(250, 253, 255);
        box-shadow:
            0px 50px 25px 5px var(--snow),
            30px 90px 25px 5px var(--snow),
            15px 130px 25px 5px var(--snow);  

    }
}

@keyframes misting{
    0% {
        box-shadow:
            0px 50px 25px 5px rgb(250, 253, 255),
            30px 90px 25px 5px rgb(250, 253, 255),
            15px 130px 25px 5px rgb(250, 253, 255);
        box-shadow:
            0px 50px 25px 5px var(--snow),
            30px 90px 25px 5px var(--snow),
            15px 130px 25px 5px var(--snow);         
    }
    50%{
        box-shadow:
            -10px 50px 10px 10px rgb(250, 253, 255),
            40px 90px 10px 10px rgb(250, 253, 255),
            5px 130px 10px 10px rgb(250, 253, 255);
        box-shadow:
            -10px 50px 10px 10px var(--snow),
            40px 90px 10px 10px var(--snow),
            5px 130px 10px 10px var(--snow);    
    }
    100%{
        box-shadow:
            0px 50px 25px 5px rgb(250, 253, 255),
            30px 90px 25px 5px rgb(250, 253, 255),
            15px 130px 25px 5px rgb(250, 253, 255);
        box-shadow:
            0px 50px 25px 5px var(--snow),
            30px 90px 25px 5px var(--snow),
            15px 130px 25px 5px var(--snow);  

    }
}

@media all and (max-width:500px){

    .moon , .sun{
        height: 120px;
        width: 120px;
    }

    .moon:hover ,.sun:hover{
        height: 130px;
        width: 130px;
    }
    
    .cloud , .Dark_cloud{
        margin-top: -20px;
        margin-left: 20px;
        width: 200px;
        height: 60px;
        border-radius: 50px;

    }
    
    .cloud:hover,.Dark_cloud:hover{
        height: 70px;
        width: 210px;
    }
    
    .cloud::before,.Dark_cloud::before{
        top:-30px;
        right:30px;
        width: 60px;
        height: 60px;
    
    }
    .cloud::after,.Dark_cloud::after{
        top:-50px;
        left: 34px;
        width: 100px;
        height: 100px;
    }
    
    .snow{
        top:130px
    }

}
